*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
#nav{
    height: 70px;
    width: 100%;
    background-color: black;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
#nav>a{
    color: white;
    text-decoration: none;
    font-family: Arial, Helvetica, sans-serif;
    text-shadow: 3px 3px 3px yellow;
}
#myform{
    height: 90vh;
    width: 100% ;
    background: linear-gradient(white, black);
    
    display: flex;
    align-items: center;
    justify-content: center;

}
#myform>table{
    border: 1px solid white;
    height: 250px;
    width: 280px;    
    border-radius: 5px;
    box-shadow: 0px 0px 5px white;

}
#myform>table label{
    color: white ;
    font-style: italic;
    font-family: Arial, Helvetica, sans-serif;
    margin-left: 10px;
}
#myform>table input{
    background-color: transparent;
    border: none;
    border-bottom: 1px solid black;
    color: white;
    outline: none;
}
#myform>table input:focus{
    outline: none;

}

 button {
    background-color: transparent;
    height: 30px;
    width: 100px;
    font-family: Arial, Helvetica, sans-serif;
    color: white;
    font-size: 18px;
    font-weight: lighter;
    border: none;
    box-shadow: 0px 0px 3px black;
    border-radius: 10px;

 }
 button :hover{
    transform: scale(1.1);
    transition: 0.5s;
    cursor: pointer;
 }
h4{
    font-style: italic;
    font-weight: bold;
    font-family: Arial, Helvetica, sans-serif;
    color: black;
    text-shadow: 0px 0px 5px white;
}
#user{
    height: 90vh;
    width: 100%;
    background: linear-gradient(white, beige, wheat);
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-wrap: wrap;
}
#cards{
    height: 200px;
    width: 200px;
    border: 4px double maroon;
    border-radius: 10px;
    font-family: Arial, Helvetica, sans-serif;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0px 0px 5px brown;
    color: darkred;
}
#cards:hover{
    transform: scale(1.1);
    transition: 0.5s;
    
}
/* #cards :hover{
    transform: scale(1.1);
    transition: 0.5s;

} */

#cards>table{
    padding: 10px;
} 
#cards td {
    color: brown;
     padding: 5px; 

}
#cards td button{
    color: black;
    height: 20px;
    width: 70px;
    display: inline-block;
}
